import React from 'react'
import * as chroma from 'chroma-js'
import { scaleRed, scaleBlue } from './Static'

class Bar extends React.Component {
  render() {
    let { grem, analyze, setAnalyze, combined, total } = this.props

    let counts = [
      combined.filter(r => r.class === 'neg').length,
      combined.filter(r => r.class === 'pos').length,
    ]

    return (
      <div
        style={{
          // background: analyze ? chroma('orchid').luminance(0.25) : 'white',
          // color: 'white',
          background: 'white',
          borderTop: 'solid 1px black',
          position: 'fixed',
          left: 0,
          bottom: 0,
          right: 0,
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        <div style={{}}>
          <button
            style={{
              paddingTop: grem / 2,
              paddingBottom: grem / 2,
              paddingLeft: grem / 4,
              paddingRight: grem / 4,
              display: 'flex',
            }}
            onClick={() => {
              if (analyze) {
                setAnalyze(false)
              } else {
                setAnalyze(true)
              }
            }}
          >
            <div style={{ paddingLeft: grem / 4, paddingRight: grem / 4 }}>
              analysis:
            </div>
            {[['on', true], ['off', false]].map(a => (
              <div
                style={{
                  paddingLeft: grem / 4,
                  paddingRight: grem / 4,
                  outline: analyze === a[1] ? 'solid 1px black' : 'none',
                  textDecoration: analyze === a[1] ? 'none' : 'underline',
                }}
              >
                {a[0]}
              </div>
            ))}
          </button>
        </div>

        <div style={{ padding: grem / 2, flexGrow: 1 }}>
          <div style={{ position: 'relative' }}>
            {analyze ? (
              <div
                style={{
                  position: 'absolute',
                  left: 0,
                  top: 0,
                  right: 0,
                  bottom: 0,
                  mixBlendMode: 'multiply',
                }}
              >
                {combined.map((c, i) => (
                  <div
                    style={{
                      position: 'absolute',
                      left: `${(1 / combined.length) * i * 100}%`,
                      top: 0,
                      bottom: 0,
                      width: `${(1 / combined.length) * 100 + 0.05}%`,
                      background:
                        c.class === 'pos'
                          ? scaleBlue(c.certainty)
                          : scaleRed(c.certainty),
                    }}
                  />
                ))}
              </div>
            ) : null}
            {combined.length} {total ? 'total ' : ''} reviews
            {analyze ? (
              <span>
                {' '}
                &middot;{' '}
                {counts[1] > counts[0] ? (
                  <span>
                    {Math.round((counts[1] / combined.length) * 100)}% positive
                  </span>
                ) : (
                  <span>
                    {Math.round((counts[0] / combined.length) * 100)}% negative
                  </span>
                )}
              </span>
            ) : null}
          </div>
        </div>
      </div>
    )
  }
}
export default Bar
